        p Las normas como la familia ISO 27000 y <em>frameworks</em> tcnicos como NIST han incorporado dentro de su metodologa las fases que llevan a que las estrategias sean evaluadas de tal manera que permita identificar debilidades y a partir de estas establecer nuevos focos de la estrategia de seguridad, teniendo como premisa, que este ejercicio debe ser permanente y continuo, debido a que las amenazas crecen de manera permanente y cada vez son ms complejas.
        p A continuacin, se reconocen algunas tcnicas que permiten realizar una evaluacin de la seguridad digital en las organizaciones y que son el insumo fundamental para determinar la efectividad y eficiencia de la estrategia propuesta: 
