            p.fondo-5.correr-izq.p-5.padding-y Los ejercicios para la gestin de vulnerabilidades pueden variar de acuerdo al tipo de organizacin y el enfoque que se le quiera dar a la gestin; pues es muy distinto realizar una gestin de vulnerabilidades en una institucin financiera o crtica frente a una pequea o mediana empresa.
    p.my-5(data-aos="fade-down-left") El procedimiento para su desarrollo ha sido basado en siete etapas principales que abordan esta gestin, entre las que se encuentran:
    .row.justify-content-center.mb-4
      .col-lg-8.col-md-12.mb-4.mb-lg-0(data-aos="fade-down-right")
        LineaTiempoD.cambiocolor
          .row.justify-content-center(numero="1" titulo="Planeacin")
            .col-xl-10.mb-4.mb-md-0
              ul.lista-ul
                li 
                  i.fas.fa-check-circle
                  | Definicin de activos
                li 
                  i.fas.fa-check-circle
                  | Planeacin de la evaluacin
              p Etapa en donde se define el alcance del procedimiento de gestin de vulnerabilidades y se identifica cul ser el campo de evaluacin o cules activos sern objetos de la revisin.
          
            .col-xl-2.col-lg-4.col-md-4.col-sm-5.col-6
              figure
                img(src='@/assets/curso/tema1/icono1.svg')


          .row.justify-content-center(numero="2" titulo="Descubrimiento")
            .col-xl-10.mb-4.mb-md-0
              ul.lista-ul
                li 
                  i.fas.fa-check-circle
                  | Recopilacin de informacin
                li 
                  i.fas.fa-check-circle
                  | Mapeo de elementos
              p Aqu se realiza la recoleccin de informacin sobre las aplicaciones, sistemas o informacin y de las vulnerabilidades que se encuentran presentes, a partir de ejercicios de escaneos que permita validar sus debilidades; como resultado de esta etapa, se debe de generar un mapeo de los elementos involucrados.
            .col-xl-2.col-lg-4.col-md-4.col-sm-5.col-6
              figure
                img(src='@/assets/curso/tema1/icono2.svg')          

          .row.justify-content-center(numero="3" titulo="Anlisis y evaluacin del riesgo")
            .col-xl-10.mb-4.mb-md-0
              ul.lista-ul
                li 
                  i.fas.fa-check-circle
                  | Identificacin de brechas
                li 
                  i.fas.fa-check-circle
                  | Determinar impacto
              p En esta etapa se establecen las brechas a la seguridad, identificando el impacto de estas sobre cada uno de los activos de informacin y cmo puede afectar la organizacin; en este ejercicio se deben de identificar y priorizar aquellas vulnerabilidades que se consideren crticas
          
            .col-xl-2.col-lg-4.col-md-4.col-sm-5.col-6
              figure
                img(src='@/assets/curso/tema1/icono3.svg')

          .row.justify-content-center(numero="4" titulo="Definicin de planes de accin")
            .col-xl-10.mb-4.mb-md-0
              ul.lista-ul
                li 
                  i.fas.fa-check-circle
                  | Diseo plan de mitigacin
                li 
                  i.fas.fa-check-circle
                  | Determina controles
              p Esta etapa debe consolidar el plan para la adopcin de acciones correctivas para el control de las vulnerabilidades; estas acciones se pueden tipificar en:
              span.text-bold Controles correctivos: 
              span controles aplicados directamente sobre sistemas o aplicaciones vulnerables.
              br
              br
              span.text-bold Controles compensatorios: 
              span controles aplicados indirectamente sobre los sistemas vulnerables.
          
            .col-xl-2.col-lg-4.col-md-4.col-sm-5.col-6
