        p #[b NIST]. Este marco de trabajo para la mejora de la seguridad ciberntica en infraestructuras crticas, el cual fue generado por el Gobierno de los Estados Unidos a mediados del ao 2014, brinda a las organizaciones la metodologa para la reduccin de riesgos cibernticos a partir de una adecuada gestin de riesgos.
        p Este marco de trabajo, propone la gestin desde una serie de funciones, las cuales se desarrollan de manera simultnea y continua como son: #[b Identificar, Proteger, Detectar, Responder y Recuperar]. Estas funciones del marco de trabajo, conlleva a desarrollar una serie de actividades basadas en categoras, encaminadas a reducir los riesgos que se pueden presentar y afectar la informacin de una organizacin. A continuacin, se puede observar un resumen de estas categoras y actividades que se desarrollan para la adecuada gestin de vulnerabilidades:
