          .col-xxl-8.col-md-7.text-white(data-aos="fade-down-left")
            h3.mb-0 OWASP
            p.mb-4.mb-md-0 Dado lo anterior, <i>owasp</i>, se ha vuelto una metodologa prctica para la identificacin de vulnerabilidades en las aplicaciones <i>web</i> ms utilizadas por los equipos de desarrollo en los ltimos aos. Se sugiere explorar y hacer uso de esta metodologa, consultando su documento oficial. Este recurso tambin est en el material complementario.
          .col-xxl-2.col-md-3(data-aos="fade-down-right")
            a.boton.color-acento-botones(:href="('https://www.nist.gov/cyberframework/framework')" target="_blank" type="application/pdf")
              span Ir al sitio <i>web</i>
              i.fas.fa-file-download
    .div(data-aos="fade-down-right").mb-5
