            img(src='@/assets/curso/tema1/figura13.svg')
          .col-xxl-8.col-md-7.text-white
            h3.mb-0 PTES
            p.mb-4.mb-md-0 Su consulta para conocer ms a detalle esta metodologa puede ser a travs de su sitio oficial, el cual tambin se encuentra en el material complementario.
          .col-xxl-2.col-md-3
            a.boton.color-acento-botones(:href="('http://www.pentest-standard.org/index.php/PTES_Technical_Guidelines')" target="_blank" type="application/pdf")
              span Ir al sitio <i>web</i>
              i.fas.fa-file-download
    p(data-aos="fade-down-right") Las metodologas anteriormente descritas permitirn realizar un anlisis de vulnerabilidades, establecer planes de acciones y un proceso importante, que es adoptar planes de mejoramiento al interior, dado que el proceso de gestin de vulnerabilidades debe ser un proceso continuo.
