    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-down-left")
      h2 1.2 Tipos de pruebas
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-2.col-md-3.col-sm-4.col-5.mb-4.mb-lg-0(data-aos="fade-down-right")
        img(src='@/assets/curso/tema1/figura27.svg')
      .col-lg-10.col-md-9.col-sm-12(data-aos="fade-down-left")
        p Cuando se est desarrollando el proceso de evaluacin de la seguridad, se debe de garantizar que los resultados presentados estn respaldados en ejercicios de verificacin y comprobacin que permitan establecer si una condicin se est cumplimento o no; para este caso estamos hablando de controles, tcnicas, tcticas para el aseguramiento de la informacin y en este ejercicio pueden surgir diferentes alternativas para verificar este tipo de cumplimientos, entre las que se pueden apreciar:
    SlyderF(data-aos="fade-down-right").mb-5(columnas="col-lg-6 col-xl-4")
      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura28.svg')
          .col-8
            h4 Pruebas unitarias
        p Este tipo de pruebas se enfocan en testear fragmentos de cdigo o aplicaciones para evaluar la lgica bajo la cual se ha programado y si desarrolla adecuadamente las tareas bajo ciertas condiciones.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura29.svg')
          .col-8
            h4 Prueba de integracin 
        p Estas pruebas, buscan verificar el desarrollo de una actividad que involucra ms de un componente, por ejemplo, la lgica de una aplicacin <i>web</i> y la de un motor de base de datos.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura30.svg')
          .col-8
            h4  Pruebas de extremo a extremo
        p Estas buscan verificar el traslado de informacin y cmo viajan entre un punto de origen y uno de destino, este tipo de pruebas son comunes cuando se prueban APIs o plataformas de envo de mensajes.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura31.svg')
          .col-8
            h4  Prueba de aceptacin
        p Este tipo de pruebas, busca validar si una solucin cumple con lo requerido o solicitado por un cliente.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura32.svg')
          .col-8
            h4  Pruebas de caja blanca
        p Este tipo de pruebas validan el comportamiento de la lgica de la solucin, para realizar este tipo de pruebas, se asume que conoce los detalles de su arquitectura, mtodos, funciones entre otros.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura33.svg')
          .col-8
            h4 Pruebas de caja negra
        p Este tipo de pruebas son de funcionalidad y comportamiento de un sistema, por lo general desconociendo la estructura de la solucin.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura34.svg')
          .col-8
            h4  Pruebas de caja gris
        p Propone una validacin, combinando las pruebas de caja blanca y caja negra, permitiendo abordar aspectos ms complejos e integrales en las soluciones.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura35.svg')
          .col-8
            h4  Prueba manual
        p Son pruebas que se especifican manualmente y sin automatizacin, para la evaluacin de los resultados obtenidos.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura36.svg')
          .col-8
            h4 Prueba esttica
        p Son aquellas que se realizan sin cdigo real, para determinar si hay defectos en el rendimiento de la solucin.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura37.svg')
          .col-8
            h4 Pruebas dinmicas
        p Este tipo de pruebas se ejecuta con cdigo real para validar su funcionamiento.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura38.svg')
          .col-8
            h4 Pruebas visuales o de interfaz de usuario
        p Son las pruebas que se realizan a interfaces de usuario para la revisin del comportamiento y la integridad de la informacin 

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura39.svg')
          .col-8
            h4 Pruebas de humo
        p Tipo de prueba que se realiza a un conjunto pequeo de controles para verificar su funcionalidad

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura40.svg')
          .col-8
            h4 Pruebas de regresin
        p Permite validar si una funcionalidad o caracterstica se rompe o no est funcionando como se propuso.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura41.svg')
          .col-8
            h4 Pruebas de carga
        p Este tipo de pruebas buscan verificar el comportamiento de una solucin de acuerdo a diferentes momentos de demanda del servicio.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura42.svg')
          .col-8
            h4 Pruebas de insercin
        p Este tipo de pruebas permite verificar la integridad de la seguridad de la informacin dentro de la solucin.
    .row.fondo-20.align-items-center
      .col-lg-8.col-md-12.mb-lg-2.mb-lg-4.p-lg-5.p-3(data-aos="fade-down-right")
        p.mt-0.mt-lg-3.mt-xl-0 Existen varios tipos de pruebas que se pueden realizar a las aplicaciones o soluciones con las que cuenta la organizacin que permite verificar si realmente estn realizando las operaciones segn lo solicitado y lo establecido en el programa.
        p Su aplicacin depender del alcance de la evaluacin a realizar, as como tambin depender si las soluciones son construidas <i>in-house</i> o son adquiridas a un tercero, en cualquiera de los casos, debera de validarse la informacin para garantizar su integridad
      .col-lg-4.col-md-6.col-sm-7.col-8.pe-0.pb-0.my-5.mostrar-lg(data-aos="fade-down-left")
        img.subir(src='@/assets/curso/tema1/imagen4.png')
