    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-down-left")
      h2 1.3 <em>Hacking</em> tico
    figure.mb-5(data-aos="fade-up-left")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/8q3cb4QwJSk" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen) 
    .row.justify-content-center.mb-5
      .col-lg-5.col-md-6.col-sm-7.col-8.mb-4.mb-lg-0(data-aos="fade-down-right")
        img(src='@/assets/curso/tema1/imagen5.png')
      .col-lg-7.col-md-12(data-aos="fade-down-left")
        p El <i>hacking</i> tico o tambin denominado sombrero blanco, es un conjunto de tcnicas que se aplican para el descubrimiento de vulnerabilidades dentro de un entorno o un conjunto de aplicaciones.
        p Esta tcnica se desarrolla de manera consentida y aprobada por las organizaciones y los responsables de realizar las validaciones correspondientes, con lo cual buscan identificar las debilidades, anormalidades y falencias en su seguridad.
        p Los expertos que realizan este tipo de validaciones hacen uso de tcnicas especializadas que se denominan <em>Test</em> de Penetracin o <em>Pentesting</em>, y su objetivo es burlar los controles de seguridad y en caso de conseguir una identificacin positiva, este deber reportarlo a la organizacin. Este tipo de estrategia no debera de causar dao, dado que es concebido y tiene fines de exploracin.
    figure.my-5(data-aos="fade-up-left")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/r3S8tscj-bg" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)    
    p.my-5(data-aos="fade-down-right") Este ejercicio se desarrolla bsicamente en las siguientes 5 fases:
    .titulo-sexto.color-acento-contenido.mt-5(data-aos="fade")
      h5 Figura 4. 
      span Fases del <em>hacking</em> tico
    .row.justify-content-center.fondo-21
      .col-12
        SlyderF(data-aos="fade-down-right").my-5(columnas="col-lg-6 col-xl-4")
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
