                img(src='@/assets/curso/tema1/figura44.svg')
            h4 Fase 2 - Escaneo
            p A partir de la informacin recolectada, inicia con el escaneo con la ayuda de herramientas para la bsqueda de vulnerabilidades.
            ul.lista-ul
              li.mb-0
                i.lista-ul__vineta
                | Escner de puertos
              li.mb-0
