            h4 Fase 3 - Obtener acceso
            p Fase en la cual explota las vulnerabilidades encontradas.
            ul.lista-ul
              li.mb-0
                i.lista-ul__vineta
                | Inyeccin de cdigo
              li.mb-0
                i.lista-ul__vineta
                | Basado en <i>web</i>
              li.mb-0
                i.lista-ul__vineta
                | Basado en red
              li.mb-0
                i.lista-ul__vineta
                | Ingeniera social
              li.mb-0
                i.lista-ul__vineta
                | Ataque de contraseas
              li.mb-0
                i.lista-ul__vineta
                | <i>Network spoofung</i>
              li.mb-0
                i.lista-ul__vineta
                | <i>Network sniffers</i>
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
            .row.mb-3
              .col-4
                img(src='@/assets/curso/tema1/figura46.svg')
            h4 Fase 4 - Mantener acceso
            p Fase en donde mantiene el sistema vulnerable.
            ul.lista-ul
              li.mb-0
