                | <i>Rootkit</i>
              li.mb-0
                i.lista-ul__vineta
                | <i>Keyloggers</i>
              li.mb-0
                i.lista-ul__vineta
                | <i>Botnets</i>
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
            .row.mb-3
              .col-4
                img(src='@/assets/curso/tema1/figura47.svg')
            h4 Fase 5 - Cubrir huella
            p Fase en la cual cubre todas las pistas de las acciones realizadas durante el ejercicio.
            ul.lista-ul
              li.mb-0 
