              li.mb-0
                i.lista-ul__vineta
                | Esteganografa
              li.mb-0
                i.lista-ul__vineta
                | <i>Tuneling</i>         
    p.my-5(data-aos="fade-down-right") Ahora bien, dependiendo de su alcance, se puede determinar el tipo de <em>hacker</em> requerido, tal como se evidencia:
    figure(data-aos="fade-up-left")
      .video
