    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-down-left")
      h2 1.4  Equipos de seguridad <em>Red Team & Blue Team</em>
    .row.justify-content-center.my-5
      .col-lg-8.col-md-12.mb-4.mb-lg-0(data-aos="fade-down-right")
        p Los equipos de seguridad <em>Red Team & Blue Team</em> se consolidan como estrategias para organizaciones ms complejas y de mayor tamao y como su nombre lo indica, est conformado por equipos de personas quienes, desde cada uno de sus grupos, realizan acciones para la identificacin de vulnerabilidades.
        p Este concepto proviene de elementos relacionados con entrenamiento militar, los cuales fueron adoptados por la disciplina de la ciberseguridad y que incorporaron para establecer estrategias de auditora desde dos frentes distintos. Veamos:
      .col-md-4.col-sm-7.col-8(data-aos="fade-down-left")
        img(src='@/assets/curso/tema1/figura58.svg')
    .row
      .col-auto.fondo-24.py-3.px-5(data-aos="fade-down-right")
        h3.mb-0.text-white.ms-4 Comparacin entre el <em>Red Team</em> y el <em>Blue Team</em>
    .row.justify-content-between.my-5(data-aos="fade-down-right")
