        .row.justify-content-cent
        er.mt-0
          .col-4.pt-0.subir1
            img.mt-0(src='@/assets/curso/tema1/figura59.svg')
          .col-8.pt-4
            h5 <em>Red Team</em>
            p Busca burlar la seguridad de la organizacin, y simular ataques para la identificacin de vulnerabilidades, haciendo uso de tcnicas de <em>pentesting</em>, herramientas especializadas e incluso de tcnicas de recoleccin de informacin convencionales, todo esto realizndose desde el exterior de la organizacin; lo anterior teniendo en cuenta:
        .row.justify-content-center
          .col-md-6.col-sm-12.mb-4.mb-lg-0.ps-4(data-aos="fade-down-right")
            ul.lista-ul
              li 
