                | Explotacin de vulnerabilidades
              li 
                i.fas.fa-check-circle
                | Pruebas de penetracin
              li 
                i.fas.fa-check-circle
                | Pruebas de caja negra
              li 
                i.fas.fa-check-circle
                | Ingeniera social
              li 
                i.fas.fa-check-circle
                | Escaneo de aplicaciones <i>web</i> 
