            img(src='@/assets/curso/tema1/figura60.svg')
      .col-lg-6.col-md-12.fondo-26.redondearpuntas.separar.pt-0(data-aos="fade-down-left")
        .row.justify-content-center.mt-0
          .col-4.pt-0.subir1
            img.mt-0(src='@/assets/curso/tema1/figura61.svg')
          .col-8.pt-4
            h5 <em>Blue Team</em>
            p Hacen frente a los ataques y defienden a la organizacin de los posibles riesgos de seguridad de la informacin, convirtindose en una primera lnea frente a la atencin a cualquier incidente de seguridad; teniendo en cuenta:
        .row.justify-content-center.mt-4
          .col-md-6.col-sm-12.mb-4.mb-lg-0.ps-4(data-aos="fade-down-right")
            ul.lista-ul
              li 
                i.fas.fa-check-circle
                | Seguridad defensiva
              li 
                i.fas.fa-check-circle
                | Proteccin de infraestructura
